import * as React from 'react';
import { navigate } from 'gatsby';
import { StaticImage } from 'gatsby-plugin-image';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Container from '@mui/material/Container';
import IconButton from '@mui/material/IconButton';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import SwipeableDrawer from '@mui/material/SwipeableDrawer';
import Typography from '@mui/material/Typography';

import LaunchIcon from '@mui/icons-material/Launch';
import LocalFloristIcon from '@mui/icons-material/LocalFlorist';
import MenuIcon from '@mui/icons-material/Menu';

import { Link } from '../components/link';
import { scrollToTop, scrollToSection } from '../utils/scroll';
import { menuHeight } from './layout';
import { NAME, PHONE } from '../../const';

const lists = [
  {
    label: 'ホーム',
    link: '/'
  },
  {
    label: '施設紹介',
    link: '/facility'
  },
  {
    label: '一日の流れ',
    link: '/flow'
  },
  /*
  {
    label: 'シナプソロジー',
    link: '/synapsology'
  },
  */
  {
    label: 'アクセス',
    link: '/#access'
  },
  {
    label: 'お問い合わせ',
    link: '/contact'
  }
];

export const Menu = ({ height }: { height: string }) => {
  const [isOpen, setIsOpen] = React.useState(false);

  const toggleDrawer = (_isOpen: boolean) => (event: any) => {
    if (
      event &&
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }

    setIsOpen(_isOpen);
  };

  return (
    <AppBar
      position='fixed'
      sx={{
        bgcolor: 'background.default'
        // boxShadow: 'none'
      }}
    >
      <Container>
        <Box
          display='flex'
          flexDirection='column'
          justifyContent='center'
          height={height}
        >
          <Box>
            <Box
              sx={{
                display: 'flex',
                //justifyContent: 'space-between'
                alignItems: 'center'
              }}
            >
              <Box mr='auto'>
                <Link
                  href='#top'
                  underline='none'
                  sx={{
                    display: 'block',
                    fontSize: 24,
                    '&:hover': {
                      transition: 'opacity 500ms ease-out',
                      opacity: 0.5
                    }
                  }}
                  onClick={e => {
                    e.preventDefault();
                    navigate('/');
                  }}
                >
                  <Box
                    sx={{
                      width: {
                        md: 240,
                        xs: 180
                      }
                    }}
                  >
                    <StaticImage
                      src='../images/logo.png'
                      alt={NAME}
                      placeholder='none'
                    />
                  </Box>
                </Link>
              </Box>
              <Box>
                <Box
                  display={{
                    md: 'flex',
                    xs: 'none'
                  }}
                  alignItems='center'
                >
                  <Link href='/contact/'>
                    <Box
                      mr={2}
                      px={2}
                      py={1}
                      display='flex'
                      justifyContent='center'
                      alignItems='center'
                      bgcolor='primary.main'
                      color='background.default'
                      sx={{
                        fontSize: 16,
                        fontWeight: 'bold',
                        borderRadius: 8
                      }}
                    >
                      <LocalFloristIcon sx={{ mr: 0.5 }} />
                      無料体験・見学会実施中
                    </Box>
                  </Link>
                  <Box>
                    <Typography
                      sx={{
                        fontSize: 13,
                        fontWeight: 'bold',
                        textAlign: 'right'
                      }}
                    >
                      お気軽にお問い合わせください
                    </Typography>
                    <Box
                      display='flex'
                      alignItems='center'
                      sx={{
                        lineHeight: 1
                      }}
                    >
                      <Box
                        component='span'
                        sx={{
                          display: {
                            md: 'block',
                            xs: 'none'
                          }
                        }}
                      >
                        TEL
                      </Box>
                      <Link
                        color='primary'
                        href={`tel:${PHONE}`}
                        sx={{
                          ml: 1,
                          fontSize: 24,
                          fontWeight: 700,
                          letterSpacing: 3
                        }}
                      >
                        {PHONE}
                      </Link>
                    </Box>
                    <Typography
                      sx={{
                        fontSize: 10,
                        mt: 0.5
                      }}
                    >
                      営業を目的としたお問い合わせはご遠慮ください
                    </Typography>
                  </Box>
                </Box>
                <Box>
                  <IconButton
                    onClick={toggleDrawer(true)}
                    sx={{
                      display: {
                        md: 'none',
                        xs: ''
                      }
                    }}
                  >
                    <MenuIcon />
                  </IconButton>
                </Box>
              </Box>
            </Box>
            <Box
              mt={1}
              sx={{
                display: {
                  md: 'block',
                  xs: 'none'
                }
              }}
            >
              {lists.map(
                ({ label, link, isCurrent, isButton, isExternal }, i) => {
                  const isAnchor = /^#.+$/.test(link);

                  const Component = (props: any) =>
                    isButton ? (
                      <Button
                        {...props}
                        variant='contained'
                        disableElevation={true}
                        sx={{ ...props.sx, borderRadius: '0px' }}
                      >
                        {props.children}
                      </Button>
                    ) : (
                      <Link underline='none' {...props}>
                        <Typography
                          color='text.primary'
                          component='span'
                          fontSize={18}
                          fontWeight={500}
                        >
                          {props.children}
                        </Typography>
                      </Link>
                    );

                  return (
                    <Component
                      key={i}
                      href={link}
                      target={isExternal && '_blank'}
                      sx={{
                        margin: '0 1em',
                        display: 'inline',
                        pointerEvents: isCurrent ? 'none' : '',
                        opacity: isCurrent ? 0.5 : 1,
                        '&:hover': {
                          opacity: 0.5
                        }
                      }}
                      onClick={(e: any) => {
                        if (!isExternal) {
                          // 外部リンクなら href 遷移
                          e.preventDefault();
                        }

                        if (isAnchor) {
                          if (link === '#top') {
                            scrollToTop();
                          } else {
                            // アンカーならスクロール
                            scrollToSection(
                              link.replace(/^#(.*)$/, '$1'),
                              menuHeight * -1
                            );
                          }
                        } else {
                          // サイト内リンクなら navigate 遷移
                          navigate(link);
                        }
                      }}
                    >
                      {label}
                    </Component>
                  );
                }
              )}
            </Box>
          </Box>
        </Box>
      </Container>

      <SwipeableDrawer
        anchor='right'
        open={isOpen}
        onClose={toggleDrawer(false)}
        onOpen={toggleDrawer(true)}
      >
        <Box
          // @TODO
          sx={{
            backgroundColor: 'background.default',
            color: 'primary.main',
            width: 200,
            height: '100%'
          }}
          role='presentation'
          onClick={toggleDrawer(false)}
          onKeyDown={toggleDrawer(false)}
        >
          <List>
            {lists.map(({ label, link, isExternal }, i) => {
              const isAnchor = /^#.+$/.test(link);

              return (
                <ListItemButton
                  key={i}
                  href={link}
                  target={isExternal && '_blank'}
                  onClick={e => {
                    if (!isExternal) {
                      // 外部リンクなら href 遷移
                      e.preventDefault();
                    }

                    if (isAnchor) {
                      // アンカーならスクロール
                      scrollToSection(
                        link.replace(/^#(.*)$/, '$1'),
                        menuHeight * -1
                      );
                    } else {
                      // サイト内リンクなら navigate 遷移
                      navigate(link);
                    }
                  }}
                >
                  <ListItemText
                    primary={
                      <Typography color='text.primary'>
                        {label}
                        {isExternal && (
                          <LaunchIcon sx={{ ml: 0.5, width: 10, height: 10 }} />
                        )}
                      </Typography>
                    }
                  />
                </ListItemButton>
              );
            })}
          </List>
        </Box>
      </SwipeableDrawer>
    </AppBar>
  );
};
